import React, { PropTypes } from 'react';
import Computer from '../../components/Computer/Computer';
import Section from '../../components/Section/Section';

import './AboutSection.scss';

import readingBible from './reading_bible.jpg';

const AboutSection = ({ zIndex }) => (
  <Section className="AboutSection" backgroundImage={readingBible} zIndex={zIndex}>
    <Computer className="AboutSection-computer">
      <div className="AboutSection-content">
        <h3>O Kongresie</h3>
        <p className="lead">Kim jest dla mnie Jezus Chrystus? Jak dowiadczy Jego obecnoci&nbsp;w&nbsp;moim yciu? Co zrobi, gdy przychodz wtpliwoci&nbsp;w&nbsp;wierze? Czy moje emocje i&nbsp;marzenia mog mnie doprowadzi do Boga? Skd wzi siy do ycia na&nbsp;co dzie? Jak nie zagubi si w&nbsp;wiecie, ktry odpycha wiar? Do czego Bg mnie wzywa? Jak rozpozna, e prowadzi&nbsp;mnie Duch wity? Jak si modli? W&nbsp;jaki&nbsp;sposb mog pomc innym ludziom?</p>
        <p>Jeeli&nbsp;stawiasz&nbsp;sobie takie lub podobne pytania, to znaczy, e chcesz&nbsp;pogbi Twoj duchowo. Duchowo chrzecijaska rozwija si dziki&nbsp;modlitwie i&nbsp;refleksji, sakramentom witym i&nbsp;sowu Boemu, dziki&nbsp;budowaniu wsplnoty i&nbsp;zaangaowaniu w&nbsp;misj. Oprcz&nbsp;tych rodkw&nbsp;Bg daje nam przewodnikw&nbsp; witych, ktrzy dziel si z&nbsp;nami&nbsp;wasn duchowoci.</p>
        <p>Do nich naley w. Jzef Kalasancjusz, niestrudzony opiekun wszystkich, ktrzy zajmuj si nauczaniem i&nbsp;wychowaniem dzieci&nbsp;i&nbsp;modziey. Jego charyzmat, misja i&nbsp;duchowo rozwijaj si w&nbsp;Kociele od 400 lat i&nbsp;dzi yj nimi&nbsp;wieccy i&nbsp;zakonnicy w&nbsp;38 krajach na&nbsp;4 kontynentach.</p>
        <p>Ten kongres jest dla wszystkich, ktrzy chc y mocniej wiar, wedug wskazwek Kalasancjusza.</p>
      </div>
    </Computer>
  </Section>
);

AboutSection.propTypes = {
  zIndex: PropTypes.number,
};

export default AboutSection;



