import React, { PropTypes } from 'react';
import Section from '../../components/Section/Section';

import piarists from './piarists.png';
import dap from './dap.png';
import liceum from './liceum.png';

import './OrganisationSection.scss';

const OrganisationSection = ({ zIndex }) => (
  <Section className="OrganisationSection" zIndex={zIndex}>
    <div className="OrganisationSection-content">
      <h4>Organizatorzy</h4>
      <div className="OrganisationSection-organisers">
        <a href="http://www.pijarzy.pl">
          <img src={piarists} alt="Herb Zakonu Pijarw" />
        </a>
        <a href="http://dap-wieczysta.pl">
          <img src={dap} alt="Logo Duszpasterstwa" />
        </a>
        <a href="http://www.liceum.pijarzy.pl">
          <img src={liceum} alt="Herb Liceum" />
        </a>
      </div>
      <h4>Dla kogo jest ten Kongres?</h4>
      <p>
        Zaproszenie kierujemy do osb penoletnich, ktre czuj si zwizane:
      </p>
      <ul>
        <li>z dzieami pijarskimi,</li>
        <li>z duszpasterstwem prowadzonym przez pijarw,</li>
        <li>z osob w. Jzefa Kalasancjusza.</li>
      </ul>
      <h4>Gdzie jest ten Kongres?</h4>
      <p><a href="http://www.liceum.pijarzy.pl">Liceum Oglnoksztacce Zakonu Pijarw w Krakowie</a> przy <a href="http://www.openstreetmap.org/relation/2410065">ul. Akacjowej 5</a></p>
      <h4>Ile kosztuje uczestnictwo?</h4>
      <ul>
        <li><strong>Pakiet A1</strong>  obejmuje udzia w caym programie Kongresu oraz obiady i kolacje (od kolacji 28 kwietnia do obiadu 1 maja)  50 z,</li>
        <li><strong>Pakiet A2</strong>  obejmuje udzia w caym programie Kongresu, zakwaterowanie w <a href="http://pl.ssm.com.pl/krakow-grochowa/">schronisku modzieowym</a> oraz niadania, obiady i kolacje (od kolacji 28 kwietnia do obiadu 1 maja)  80 z.</li>
      </ul>
    </div>
  </Section>
);

OrganisationSection.propTypes = {
  zIndex: PropTypes.number,
};

export default OrganisationSection;



